import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { EducationalTabelsRoutingModule } from './educational-tabels-routing.module';
import { WorkDayIndexComponent } from './work-day-index/work-day-index.component';
import { SharedModule } from 'app/shared/shared.module';
import { NgxSpinnerModule } from 'ngx-spinner';
import { MaterialModule } from 'app/shared/material/material.module';
import { ReactiveFormsModule } from '@angular/forms';
import { DateAdapter, MAT_DATE_FORMATS } from '@angular/material/core';
import { AppDateAdapter, APP_DATE_FORMATS } from 'app/shared/validators/date.adapter';
import { WorkDayDetailsComponent } from './work-day-details/work-day-details.component';
import { DailyTableComponent } from './daily-table/daily-table.component';
import { AddEducationalDalyTableComponent } from './add-educational-daly-table/add-educational-daly-table.component';
import { TablePreviewComponent } from './table-preview/table-preview.component';
import { UploadEductioanlTableFileComponent } from './upload-eductioanl-table-file/upload-eductioanl-table-file.component';

@NgModule({
  declarations: [
    WorkDayIndexComponent,
    WorkDayDetailsComponent,
    DailyTableComponent,
    AddEducationalDalyTableComponent,
    TablePreviewComponent,
    UploadEductioanlTableFileComponent
  ],
  imports: [
    CommonModule,
    SharedModule,
    NgxSpinnerModule,
    MaterialModule,
    ReactiveFormsModule,
    EducationalTabelsRoutingModule
  ],
  providers: [
    {
      provide: DateAdapter,
      useClass: AppDateAdapter,
    },
    {
      provide: MAT_DATE_FORMATS,
      useValue: APP_DATE_FORMATS,
    },
  ],
})
export class EducationalTabelsModule { }
