import { Component, OnInit, ViewChild } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { MatDialog } from "@angular/material/dialog";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { MatTableDataSource } from "@angular/material/table";
import { ActivatedRoute } from "@angular/router";
import { TranslateService } from "@ngx-translate/core";
import {
  IWeeklyDayVM,
  IWorkDayAddEditVM,
  IWorkDayVM,
} from "app/dashboard/models/educational-tabels/weekly-table";
import { ITermGetVM } from "app/dashboard/models/setup/educational-setup/terms";
import { WeeklyTableService } from "app/dashboard/services/educational-tabels/weekly-table.service";
import { TermsService } from "app/dashboard/services/setup/educational-setup/terms.service";
import { DeleteDialogComponent } from "app/shared/components/dialogs/delete-dialog/delete-dialog.component";
import { IRowFunctionVM } from "app/shared/models/general/general";
import { AuthService } from "app/shared/services/auth/auth.service";
import { MessagesService } from "app/shared/services/messages.service";
import { CustomeValidator } from "app/shared/validators/customeValid.validator";
import { NgxSpinnerService } from "ngx-spinner";

@Component({
  selector: "app-work-day-index",
  templateUrl: "./work-day-index.component.html",
  styleUrls: ["./work-day-index.component.scss"],
})
export class WorkDayIndexComponent implements OnInit {
  myForm: FormGroup;
  submitted: boolean = false;

  WeeklyDays: IWeeklyDayVM[];
  TermList: ITermGetVM[];
  itemsCounts: number = 0;

  curLang: string;
  EduCompId: number;

  functionId: number;
  rowFunctions: IRowFunctionVM[];
  // rowFunctions: any[] = [
  //   { functionName: "Edit" },
  //   { functionName: "Delete" },
  //   { functionName: "Details" },
  // ];

  displayColumns: string[] = [
    "#",
    "AcademicEduYear_From_Date",
    "AcademicEduYear_To_Date",
    "EduYear",
    "Term",
    "WeeklyDay",
    "Lecture_Num",
    "IsWorkDay",
    "actions",
  ];
  dataSource: MatTableDataSource<IWorkDayVM>;
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;

  constructor(
    private spinner: NgxSpinnerService,
    private toastr: MessagesService,
    private dialog: MatDialog,
    private translate: TranslateService,
    private fb: FormBuilder,
    private WeeklyTableServ: WeeklyTableService,
    private TermsServ: TermsService,
    private route: ActivatedRoute,
    public authService: AuthService
  ) {
    route.queryParamMap.subscribe((params) => {
      this.functionId = Number(params.get("functionId"));
      if (this.functionId) {
        this.authService
          .getUserRowFunctions(this.functionId)
          .subscribe((res: any) => {
            if (res.length > 0) {
              this.rowFunctions = res;
            }
          });
      }
    });

    this.EduCompId = Number(authService.getEduCompId());
  }

  ngOnInit(): void {
    this.myForm = this.fb.group({
      id: [0],
      AcademicEduYearId: ["", [Validators.required]],
      TermId: [
        {
          value: [],
          disabled: true,
        },
        [Validators.required],
      ],
      WeeklyDay_ID: ["", [Validators.required]],
      Lecture_Num: [
        "",
        [Validators.required, CustomeValidator.minusNum, Validators.max(7)],
      ],
      Break_Num: [
        "",
        [Validators.required, CustomeValidator.minusNum],
      ],
      IsWorkDay: ["", [Validators.required]],
    });

    this.translate.onLangChange.subscribe((lang) => {
      this.curLang = lang.lang;
    });

    this.FormCtrls.AcademicEduYearId.valueChanges.subscribe(
      (academicYearId) => {
        // console.log(academicYearId);
        this.FormCtrls.TermId.setValue(null);

        if (academicYearId) {
          this.spinner.show();
          this.TermsServ.getTermsByYearIdDropdown(academicYearId).subscribe(
            (res) => {
              if (res) {
                this.TermList = res;
                this.FormCtrls.TermId.enable();
              }
              this.spinner.hide();
            }
          );
        } else this.FormCtrls.TermId.disable();
      }
    );

    this.getAllWorkDays(1);
    this.getAllWeeklyDays();
  }

  ngAfterViewInit() {
    if (this.dataSource) {
      this.dataSource.paginator = this.paginator;
      this.startSortingTable();
    }

    this.authService.EduCompId.subscribe((e) => {
      this.EduCompId = e.EduCompId;
      this.getAllWorkDays(1);
      this.getAllWeeklyDays();
    });
  }

  startSortingTable() {
    // custom sort
    this.dataSource.sortingDataAccessor = (item, property) => {
      switch (property) {
        case "WeeklyDay":
          return this.curLang == "ar"
            ? item.WeeklyDay_Ar_Name ?? item.WeeklyDay_En_Name
            : item.WeeklyDay_En_Name ?? item.WeeklyDay_Ar_Name;
        case "Term":
          return this.curLang == "ar"
            ? item.Term_Ar_Name ?? item.Term_En_Name
            : item.Term_En_Name ?? item.Term_Ar_Name;
        case "EduYear":
          return this.curLang == "ar"
            ? item.EducationalYear_Ar_Name ?? item.EducationalYear_En_Name
            : item.EducationalYear_En_Name ?? item.EducationalYear_Ar_Name;
        default:
          return item[property];
      }
    };
    this.dataSource.sort = this.sort;
  }

  get FormCtrls() {
    return this.myForm.controls;
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }

  // Return Weekly Days
  getAllWeeklyDays() {
    this.WeeklyTableServ.getAllWeeklyDays().subscribe((res) => {
      if (res) this.WeeklyDays = res;
    });
  }

  // Return Educational Year Details
  getAllWorkDays(page: number) {
    this.spinner.show();
    this.WeeklyTableServ.getAllWorkDaysByPage(page).subscribe((res) => {
      if (res) {
        this.dataSource = new MatTableDataSource(res.workDaysList);
        this.itemsCounts = res.countItems;
        this.startSortingTable();
      }
      this.spinner.hide();
    });
  }

  editWorkDay(workDay: IWorkDayVM) {
    this.myForm.patchValue(workDay);
    this.FormCtrls.TermId.setValue(workDay.TermId);
  }

  onSubmit() {
    this.submitted = true;

    let workDay: IWorkDayAddEditVM = {
      id: this.FormCtrls.id.value,
      WeeklyDay_ID: this.FormCtrls.WeeklyDay_ID.value,
      TermId: this.FormCtrls.TermId.value,
      Lecture_Num: this.FormCtrls.Lecture_Num.value,
      Break_Num: this.FormCtrls.Break_Num.value,

      IsWorkDay: this.FormCtrls.IsWorkDay.value,
      // eduCompId: this.EduCompId,
    };
    console.log(workDay);

    if (this.myForm.valid) {
      this.spinner.show();
      this.WeeklyTableServ.addEditWorkDay(workDay).subscribe((res: any) => {
        if (res.returnValue == 1) {
          // let returnObject = res.work_days;
          // let returnData: IWorkDayVM = {
          //   id: returnObject.id,
          //   WeeklyDay_ID: returnObject.WeeklyDay_ID,
          //   WeeklyDay_Ar_Name: returnObject.WeeklyDay_Ar_Name,
          //   WeeklyDay_En_Name: returnObject.WeeklyDay_En_Name,
          //   Lecture_Num: returnObject.Lecture_Num,
          //   TermId: returnObject.TermId,
          //   Term_Ar_Name: returnObject.Term_Ar_Name,
          //   Term_En_Name: returnObject.Term_En_Name,
          // };

          workDay.id == 0
            ? this.addWorkDay(res.work_days)
            : this.updateWorkDay(res.work_days);
          this.dataSource.sort = this.sort;
          // this.clearForm();
        } else this.toastr.error(res?.returnString);
        this.spinner.hide();
      });
    } else this.toastr.error("General.FormNotValid");
  }

  // Add Educational Year Detail
  addWorkDay(res: IWorkDayVM) {
    this.dataSource.data.unshift(res);
    this.itemsCounts++;
    this.toastr.success("General.AddSuccessfully");

    this.FormCtrls.WeeklyDay_ID.reset();
    this.FormCtrls.Lecture_Num.reset();
    this.FormCtrls.IsWorkDay.reset();
    this.submitted = false;
  }

  // Update Educational Year Detail
  updateWorkDay(res: IWorkDayVM) {
    let i = this.dataSource.data.findIndex((e) => e.id == res.id);
    this.dataSource.data[i] = res;
    this.toastr.success("General.UpdateSuccessfully");

    this.clearForm();
  }

  openDeleteDialog(workDay: IWorkDayVM): void {
    let weeklyDayName =
      this.curLang == "ar"
        ? workDay.WeeklyDay_Ar_Name ?? workDay.WeeklyDay_En_Name
        : workDay.WeeklyDay_En_Name ?? workDay.WeeklyDay_Ar_Name;
    let termName =
      this.curLang == "ar"
        ? workDay.Term_Ar_Name ?? workDay.Term_En_Name
        : workDay.Term_En_Name ?? workDay.Term_Ar_Name;

    this.dialog
      .open(DeleteDialogComponent, {
        data: {
          msg: `${termName} - ${weeklyDayName}`,
        },
      })
      .afterClosed()
      .subscribe((confirm) => {
        if (confirm) this.deleteWorkDay(workDay);
      });
  }

  // Start:Delete Educational Year Detail
  deleteWorkDay(workDay: IWorkDayVM) {
    this.WeeklyTableServ.deleteWorkDay(workDay.id).subscribe((res: any) => {
      if (res.returnValue == 1) {
        let index = this.dataSource.data.findIndex((e) => e == workDay);
        this.dataSource.data.splice(index, 1);
        // this.itemsCounts--;
        this.dataSource.sort = this.sort;

        if (workDay.id == this.FormCtrls.id.value) this.clearForm();

        this.toastr.success("General.DeleteSuccessfully");
      } else {
        this.toastr.error("General.TosterErrorMessage");
      }
    });
  }

  clearForm() {
    this.myForm.reset();
    this.FormCtrls.id.setValue(0);
    this.submitted = false;
  }
}
