import { Component, OnInit } from "@angular/core";
import { FormArray, FormBuilder, FormGroup, Validators } from "@angular/forms";
import { MatDialog } from "@angular/material/dialog";
import { ActivatedRoute } from "@angular/router";
import { TranslateService } from "@ngx-translate/core";
import {
  IWorkDayDetailsAddEditVM,
  IWeeklyLectureVM,
  IWorkDayDetailsVM,
} from "app/dashboard/models/educational-tabels/weekly-table";
import { WeeklyTableService } from "app/dashboard/services/educational-tabels/weekly-table.service";
import { DeleteDialogComponent } from "app/shared/components/dialogs/delete-dialog/delete-dialog.component";
import { AuthService } from "app/shared/services/auth/auth.service";
import { MessagesService } from "app/shared/services/messages.service";
import { NgxSpinnerService } from "ngx-spinner";
import { forkJoin } from "rxjs";

@Component({
  selector: "app-work-day-details",
  templateUrl: "./work-day-details.component.html",
  styleUrls: ["./work-day-details.component.scss"],
})
export class WorkDayDetailsComponent implements OnInit {
  myForm: FormGroup;
  submitted: boolean = false;

  WorkDayDetails: IWorkDayDetailsVM[] = [];
  WeeklyLectures: IWeeklyLectureVM[];
  workDayId: number = 0;
  LectureNo: number = 0;
  BreakNo: number;

  curLang: string;
  EduCompId: number;

  functionId: number;

  constructor(
    private spinner: NgxSpinnerService,
    private toastr: MessagesService,
    private dialog: MatDialog,
    private translate: TranslateService,
    private fb: FormBuilder,
    private WeeklyTableServ: WeeklyTableService,
    private route: ActivatedRoute,
    public authService: AuthService
  ) {
    route.queryParamMap.subscribe((params) => {
      this.functionId = Number(params.get("functionId"));
    });

    // get data from url
    route.paramMap.subscribe((params) => {
      this.LectureNo = Number(params.get("LecNo"));
      this.workDayId = Number(params.get("workDayId"));
      this.BreakNo = Number(params.get("BreakNo"));
    });

    this.translate.onLangChange.subscribe((lang) => {
      this.curLang = lang.lang;
    });

    this.EduCompId = Number(authService.getEduCompId());
  }

  // Return Weekly Days
  getAllWeeklyLectures() {
    return this.WeeklyTableServ.getAllWeeklyLectures();
  }

  // Return work day Details
  getAllWorkDayDetails() {
    return this.WeeklyTableServ.getAllWorkDayDetailsByWorkDayId(this.workDayId);
  }

  getListsData() {
    forkJoin([
      this.getAllWeeklyLectures(),
      this.getAllWorkDayDetails(),
    ]).subscribe((res) => {
      this.WeeklyLectures = res[0];
      // this.WorkDayDetails = res[1];
      this.getAllFormValues(res[1]);

      // console.log("all Lectures", res[0]);
      // console.log("Day Lectures", res[1]);
    });
  }

  ngOnInit(): void {
    this.myForm = this.fb.group({
      lecDetails: this.fb.array([]),
    });

    this.getListsData();
  }

  ngAfterViewInit() {
    this.authService.EduCompId.subscribe((e) => {
      this.EduCompId = e.EduCompId;
      this.getListsData();
    });
  }

  get lecDetailsArr() {
    return this.myForm.controls["lecDetails"] as FormArray;
  }

  lecDetailsForm(index: number) {
    return this.lecDetailsArr.controls[index] as FormGroup;
  }

  getAllFormValues(WorkDayDetailsArr: any[]) {
    let allLecAndBreak = this.BreakNo + this.LectureNo;
    // get froms data
    console.log("Day Lectures", WorkDayDetailsArr);

    for (let i = 0; i < allLecAndBreak; i++) {
      this.addlecDetails();
      let workDayDetail: IWorkDayDetailsVM = WorkDayDetailsArr.find(
        (workDayDetail) => {
          if (workDayDetail.Weekly_Lec_ID != null)
            return workDayDetail.Weekly_Lec_ID == this.WeeklyLectures[i]?.id;
        }
      );
      // console.log(workDayDetail);

      if (workDayDetail) {
        this.lecDetailsForm(i).patchValue(workDayDetail);
        this.lecDetailsForm(i).controls.id.setValue(
          workDayDetail.workDayDetailsId
        );
      } else {
        if (i < this.LectureNo) {
          this.lecDetailsForm(i).controls.weeklyLec_Ar_Name.setValue(
            this.WeeklyLectures[i].Ar_Name
          );
          this.lecDetailsForm(i).controls.weeklyLec_En_Name.setValue(
            this.WeeklyLectures[i].En_Name
          );
          this.lecDetailsForm(i).controls.Weekly_Lec_ID.setValue(
            this.WeeklyLectures[i].id
          );
        }
      }
      // to sort WorkDayDetails array by Weekly_Lec_ID (lectures in order)
      this.WorkDayDetails.push(this.lecDetailsForm(i).value);
    }

    // get Breaks indexs from Form Array then save index as id in FormBreakIndexs
    let FormBreakIndexs = [];
    this.lecDetailsArr.value.forEach((ele) => {
      if (ele.Weekly_Lec_ID == null) {
        let index = this.lecDetailsArr.value.findIndex((e) => e == ele);
        // console.log(index, ele);
        // save index as id in FormBreakIndexs
        FormBreakIndexs.push({ id: index });
      }
    });
    console.log("FormBreakIndexs", FormBreakIndexs);

    let noBreakIntered = 0;
    //Get Day Breaks From Day Lecuters
    WorkDayDetailsArr.forEach((ele, dayIndex) => {
      if (ele.Weekly_Lec_ID == null) {
        // console.log("Break_Item", ele);

        //take Break index from FormBreakIndexs var
        FormBreakIndexs.forEach((breakIndex, i) => {
          // console.log(dayIndex, i);

          //set Break values to form
          if (noBreakIntered === i) {
            // console.log(breakIndex.id, i, noBreakIntered, ele);

            this.lecDetailsForm(breakIndex.id).patchValue(ele);
            this.lecDetailsForm(breakIndex.id).controls.id.setValue(
              ele.workDayDetailsId
            );
          } else if (
            dayIndex != i &&
            ele.From_Hour == null &&
            ele.To_Hour == null
          ) {
            this.lecDetailsForm(breakIndex.id).controls.From_Hour.setValue(
              null
            );
            this.lecDetailsForm(breakIndex.id).controls.To_Hour.setValue(null);
            this.lecDetailsForm(breakIndex.id).controls.Weekly_Lec_ID.setValue(
              null
            );
            this.lecDetailsForm(breakIndex.id).controls.id.setValue(0);
          }
        });
        noBreakIntered++;
      }
      //  this.lecDetailsForm(index).patchValue(ele);
    });

    // console.log("Form", this.lecDetailsArr.value);
  }

  addlecDetails() {
    let lecDetailsForm = this.fb.group({
      id: [0],
      Weekly_Lec_ID: [null],
      weeklyLec_Ar_Name: [""],
      weeklyLec_En_Name: [""],
      //Subject_id: ["", [Validators.required]],
      From_Hour: ["", [Validators.required]],
      To_Hour: ["", [Validators.required]],
      submitted: [false],
    });
    // console.log(lecDetailsForm);

    this.lecDetailsArr.push(lecDetailsForm);
  }

  onSubmit(formIndex: number) {
    // current form controls
    let curForm = this.lecDetailsForm(formIndex).controls;

    curForm.submitted.setValue(false);

    if (curForm.To_Hour.value < curForm.From_Hour.value) {
      this.toastr.error("EducationalTabels.ToHourBeforeFromDHour");
      return;
    }

    let workDay: IWorkDayDetailsAddEditVM = {
      id: curForm.id.value,
      WorkDay_ID: this.workDayId,
      Weekly_Lec_ID: curForm.Weekly_Lec_ID.value,
      // Subject_id: curForm.Subject_id.value,
      From_Hour: curForm.From_Hour.value,
      To_Hour: curForm.To_Hour.value,
      // eduCompId: this.EduCompId,
    };
    console.log("lllllllll", workDay);

    if (this.lecDetailsForm(formIndex).valid) {
      this.spinner.show();
      this.WeeklyTableServ.addEditWorkDayDetails(workDay).subscribe(
        (res: any) => {
          if (res.returnValue == 1) {
            this.lecDetailsForm(formIndex).patchValue(res.workDay_Details);
            this.WorkDayDetails[formIndex] = res.workDay_Details;
            workDay.id == 0
              ? this.toastr.success("General.AddSuccessfully")
              : this.toastr.success("General.UpdateSuccessfully");
          } else this.toastr.error(res?.returnString);
          this.spinner.hide();
        }
      );
    } else this.toastr.error("General.FormNotValid");
  }

  openDeleteDialog(formIndex: number): void {
    // current form controls
    let curForm = this.lecDetailsForm(formIndex).controls;
    let lecName =
      this.curLang == "ar"
        ? curForm.weeklyLec_Ar_Name.value
        : curForm.weeklyLec_En_Name.value;

    this.dialog
      .open(DeleteDialogComponent, {
        data: {
          msg: lecName,
        },
      })
      .afterClosed()
      .subscribe((confirm) => {
        if (confirm) this.deleteWorkDay(formIndex);
      });
  }

  // Delete work day Detail
  deleteWorkDay(formIndex: number) {
    // current form controls
    let curForm = this.lecDetailsForm(formIndex).controls;

    this.WeeklyTableServ.deleteWorkDayDetails(curForm.id.value).subscribe(
      (res: any) => {
        if (res.returnValue == 1) {
          curForm.id.setValue(0);
          this.clearForm(formIndex);
          this.toastr.success("General.DeleteSuccessfully");
        } else {
          this.toastr.error("General.TosterErrorMessage");
        }
      }
    );
  }

  clearForm(formIndex: number) {
    // console.log(this.lecDetailsForm(formIndex));
    let curForm: FormGroup = this.lecDetailsForm(formIndex);

    // add > reset form values
    if (curForm.controls.id.value == 0) {
      // curForm.controls.Subject_id.reset();
      curForm.controls.From_Hour.reset();
      curForm.controls.To_Hour.reset();
      curForm.controls.submitted.setValue(false);
    }
    // update > return exist (saved) values
    else curForm.patchValue(this.WorkDayDetails[formIndex]);
  }
}
