import { DailyTableComponent } from "./daily-table/daily-table.component";
import { WorkDayDetailsComponent } from "./work-day-details/work-day-details.component";
import { WorkDayIndexComponent } from "./work-day-index/work-day-index.component";
import { NgModule } from "@angular/core";
import { RouterModule, Routes } from "@angular/router";
import { TableCalenderComponent } from "app/shared/components/edu-tabels/table-calender/table-calender.component";
import { AddEducationalDalyTableComponent } from "./add-educational-daly-table/add-educational-daly-table.component";
import { TablePreviewComponent } from "./table-preview/table-preview.component";

const routes: Routes = [
  {
    path: "work-day",
    children: [
      {
        path: "",
        redirectTo: "index",
        pathMatch: "full",
      },
      {
        path: "index",
        component: WorkDayIndexComponent,
      },
      {
        path: "details/:workDayId/:LecNo/:BreakNo",
        component: WorkDayDetailsComponent,
      },
    ],
  },
  {
    path: "daily-table",
    component: DailyTableComponent,
  },

  {
    path: "calender-table",
    component: TablePreviewComponent,
  },
  {
    path: "add-table",
    component: AddEducationalDalyTableComponent,
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class EducationalTabelsRoutingModule {}
