import { Component, OnInit } from "@angular/core";
import { FormControl, FormGroup, Validators } from "@angular/forms";
import { FormBuilder } from "@angular/forms";
import { IUploadTableFile } from "app/dashboard/models/educational-tabels/weekly-table";
import { WeeklyTableService } from "app/dashboard/services/educational-tabels/weekly-table.service";
import { IfileVM } from "app/shared/models/general/general";
import { UploadFileService } from "app/shared/services/General/upload-file.service";
import { MessagesService } from "app/shared/services/messages.service";

@Component({
  selector: "app-upload-eductioanl-table-file",
  templateUrl: "./upload-eductioanl-table-file.component.html",
  styleUrls: ["./upload-eductioanl-table-file.component.scss"],
})
export class UploadEductioanlTableFileComponent implements OnInit {
  myForm: FormGroup;
  constructor(
    private fb: FormBuilder,
    private WeeklyTableServ: WeeklyTableService,
    private uploadFileService: UploadFileService,
    private toastr: MessagesService

  ) {}

  ngOnInit(): void {
    this.myForm = this.fb.group({
      file: ["", Validators.required],
    });
  }
  get FormCtrls() {
    return this.myForm.controls;
  }

  onChang($event) {
    this.uploadFileService.uploadFile($event).then((file: IfileVM) => {
      console.log("file", file);
      this.FormCtrls.file?.setValue(file);
    });
  }
  upload(){
    let data:IUploadTableFile={
      uploadedFile:this.FormCtrls.file.value
    }

      if (this.myForm.valid) {
        console.log(this.FormCtrls.file.value);
        this.WeeklyTableServ.uploadEducationalTable(data).subscribe((res)=>{
          if (res) {
            const FileInputs = document.querySelectorAll('input[type="file"]');
            FileInputs.forEach((input: any) => {
              input.value = null;
            });
            this.toastr.success("General.AddSuccessfully");
          }
        });
      } else this.toastr.error("General.FormNotValid");

  }
}
