import { Component, OnInit } from "@angular/core";
import { FormArray, FormBuilder, FormGroup, Validators } from "@angular/forms";
import { MatDialog } from "@angular/material/dialog";
import { ActivatedRoute } from "@angular/router";
import { TranslateService } from "@ngx-translate/core";
import {
  IEducationalAddEditVM,
  ILectureDataVM,
  IWeeklyLectureVM,
  IWorkDayDetailsAddEditVM,
  IWorkDayDetailsVM,
  IWorkDayVM,
} from "app/dashboard/models/educational-tabels/weekly-table";
import { WeeklyTableService } from "app/dashboard/services/educational-tabels/weekly-table.service";
import { CourseLectureService } from "app/dashboard/services/setup/educational-setup/course-lecture.service";
import { DeleteDialogComponent } from "app/shared/components/dialogs/delete-dialog/delete-dialog.component";
import { AuthService } from "app/shared/services/auth/auth.service";
import { MessagesService } from "app/shared/services/messages.service";
import { NgxSpinnerService } from "ngx-spinner";
import { forkJoin } from "rxjs";
import { UploadEductioanlTableFileComponent } from "../upload-eductioanl-table-file/upload-eductioanl-table-file.component";

@Component({
  selector: "app-add-educational-daly-table",
  templateUrl: "./add-educational-daly-table.component.html",
  styleUrls: ["./add-educational-daly-table.component.scss"],
})
export class AddEducationalDalyTableComponent implements OnInit {
  curLang: string;
  EduCompId: number;
  myForm: FormGroup;
  submitted: boolean = false;
  dayList: IWorkDayVM[];
  functionId: number;
  lectureNo: number;
  WorkDayDetails: IWorkDayDetailsVM[] = [];
  WeeklyLectures: IWeeklyLectureVM[];
  teatcherList: any;
  lecturSubjectData: ILectureDataVM;

  constructor(
    private spinner: NgxSpinnerService,
    private toastr: MessagesService,
    private dialog: MatDialog,

    private translate: TranslateService,
    private fb: FormBuilder,
    private route: ActivatedRoute,
    public authService: AuthService,
    private WeeklyTableServ: WeeklyTableService
  ) {
    route.queryParamMap.subscribe((params) => {
      this.functionId = Number(params.get("functionId"));
    });
    this.translate.onLangChange.subscribe((lang) => {
      this.curLang = lang.lang;
    });
    this.EduCompId = Number(authService.getEduCompId());
  }
  ngAfterViewInit() {
    this.authService.EduCompId.subscribe((e) => {
      this.EduCompId = e.EduCompId;
    });
  }

  ngOnInit(): void {
    this.getAllWorkDayes();
    this.myForm = this.fb.group({
      educationalYearId: ["", Validators.required],
      AcaademicClassRoomId: ["", Validators.required],
      dayId: ["", Validators.required],
      lectureForm: this.fb.array([]),
    });
  }

  lectureForm() {
    let lecSubjectForm = this.fb.group({
      id: [0],
      Weekly_Lec_ID: [null, [Validators.required]],
      weeklyLec_Ar_Name: [null],
      weeklyLec_En_Name: [""],
      teacherSubjectId: ["", [Validators.required]],
      Subject_id: ["", [Validators.required]],
      submitted: [false],
    });
    this.lectureSubjectArr.push(lecSubjectForm);
  }

  get lectureSubjectArr() {
    //to get lecture control
    return this.myForm.controls["lectureForm"] as FormArray;
  }
  get FormCtrls() {
    return this.myForm.controls;
  }
  // Return  Days

  getAllWorkDayes() {
    this.WeeklyTableServ.getAllWorkDays().subscribe((res: IWorkDayVM[]) => {
      this.dayList = res;
    });
  }

  // Return Educational Table  Details
  getALLEducationalTableWorkDayDetails(workDayId: number) {
    console.log("id",workDayId)
    return this.WeeklyTableServ.getWorkDayById(workDayId);
  }

  //return lecture data
  getLectureDetail(lecDayId: number, index: number) {
    return this.WeeklyTableServ.getAllectureDetail(lecDayId,this.FormCtrls.AcaademicClassRoomId.value).subscribe(
      (res: ILectureDataVM) => {
        if (res[0]) {
          this.lecDetailsForm(index).patchValue(res[0])


                this.lecDetailsForm(index).controls.teacherSubjectId.setValue(
                  res[0].TeacherSubjectId
                );
                // this.lecDetailsForm(index).controls.id.setValue(
                //   res[0].TeacherSubjectId
                // );

        }
      }
    );
  }


  patchAllLectureDetail(){
    console.log("day",this.WorkDayDetails)

    for(var i=0;i<this.lectureNo;i++){
      if(this.WorkDayDetails[i]?.Weekly_Lec_ID!=null)
         this.getLectureDetail(this.WorkDayDetails[i]?.workDayDetailsId,i);
    }

  }
  //to get current control
  lecDetailsForm(index: number) {
    return this.lectureSubjectArr.controls[index] as FormGroup;
  }

  getNumberOFlecture() {
    forkJoin([
      // this.getAllWeeklyLectures(),
      this.getALLEducationalTableWorkDayDetails(this.FormCtrls.dayId.value),
    ]).subscribe((val) => {
      // this.WeeklyLectures = val[0];
      this.WorkDayDetails = val[0];
      this.lectureNo = this.WorkDayDetails[0].Lecture_Num;

      this.getAllLectureByDay();

    });
  }

  getAllLectureByDay() {
    this.lectureSubjectArr.clear();
    console.log("get",this.WorkDayDetails)

    for (var i = 0; i < this.lectureNo; i++) {
      this.lectureForm();
     //let name=this.WeeklyLectures[i].Ar_Name+this.WorkDayDetails[i].workDayDetailsId

      this.lecDetailsForm(i).controls.Weekly_Lec_ID.setValue(
        this.WorkDayDetails[i]?.Weekly_Lec_ID??"0"
      );
      this.lecDetailsForm(i).controls.weeklyLec_Ar_Name.setValue(
       this.WorkDayDetails[i]?.weeklyLec_En_Name??"null"

      );
      this.lecDetailsForm(i).controls.weeklyLec_En_Name.setValue(
        this.WorkDayDetails[i]?.weeklyLec_Ar_Name??"null"
      );

    }
    this.patchAllLectureDetail()

  }

  onSubmit(index: number) {
    let curntControl = this.lecDetailsForm(index).controls;
    curntControl.submitted.setValue(false);

    let tableData = {} as IEducationalAddEditVM;
    tableData.id = curntControl.id.value;
    tableData.AcaademicClassRoomId = this.FormCtrls.AcaademicClassRoomId.value;
    tableData.WorkDayDetailsId = this.WorkDayDetails[index].workDayDetailsId;
    tableData.teacherSubjectId = curntControl.teacherSubjectId.value;
    console.log("add", tableData);
    console.log("valid", this.lecDetailsForm(index).valid);

    if (
      this.FormCtrls.AcaademicClassRoomId.value != "" &&
      this.lecDetailsForm(index).valid
    ) {
      this.spinner.show();

      this.WeeklyTableServ.addEditEducationalTable(tableData).subscribe(
        (res: any) => {
          if (res.returnValue == 1) {
            this.lecDetailsForm(index).patchValue(res.EduTable_Details);
            this.WorkDayDetails[index] = res.EduTable_Details;
            tableData.id == 0
              ? this.toastr.success("General.AddSuccessfully")
              : this.toastr.success("General.UpdateSuccessfully");
          } else this.toastr.error(res?.returnString);
          this.spinner.hide();
        }
      );
    } else this.toastr.error("General.FormNotValid");
  }
  openDeleteDialog(formIndex: number): void {
    // current form controls
    let curForm = this.lecDetailsForm(formIndex).controls;
    let lecName =
      this.curLang == "ar"
        ? curForm.weeklyLec_Ar_Name.value
        : curForm.weeklyLec_En_Name.value;

    this.dialog
      .open(DeleteDialogComponent, {
        data: {
          msg: lecName,
        },
      })
      .afterClosed()
      .subscribe((confirm) => {
        if (confirm) this.deleteWorkDay(formIndex);
      });
  }
 // upload file
  openUploadFileDialog(): void {

      this.dialog
        .open(UploadEductioanlTableFileComponent, {

        })
        .afterClosed()

    }
  // Start:Delete Educational Year Detail
  deleteWorkDay(formIndex: number) {
    // current form controls
    let curForm = this.lecDetailsForm(formIndex).controls;

    this.WeeklyTableServ.deleteWorkDayDetails(curForm.id.value).subscribe(
      (res: any) => {
        if (res.returnValue == 1) {
          curForm.id.setValue(0);
          this.clearForm(formIndex);
          this.toastr.success("General.DeleteSuccessfully");
        } else {
          this.toastr.error("General.TosterErrorMessage");
        }
      }
    );
  }

  clearForm(formIndex: number) {
    // console.log(this.lecDetailsForm(formIndex));
    let curForm: FormGroup = this.lecDetailsForm(formIndex);

    // add > reset form values
    if (curForm.controls.id.value == 0) {
      // curForm.controls.Subject_id.reset();
      curForm.controls.teacherSubjectId.reset();
      curForm.controls.Subject_id.reset();
      curForm.controls.submitted.setValue(false);
    }
    // update > return exist (saved) values
    else curForm.patchValue(this.WorkDayDetails[formIndex]);
  }
}
